.post {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  box-shadow: 0 0 3px rgba(0, 0, 128, 0.3);
  width: 100%;
  /*max-width: 50%;*/
  margin: 20px auto;
  padding: 15px 10px;
  background: #fff;
  overflow-wrap: normal;
  /*overflow-x: scroll;*/

  /*grid-column: ;*/
  /*grid-row: 2;*/
}

.post:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  background: rgba(0, 0, 0, 0.1);
}

.h1:hover {
  background-color: green;
  cursor: pointer;
}

.wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
  grid-auto-rows: minmax(100px, auto);
}

.container {
  /*display: flex;*/
  /*flex-direction: row;*/
  padding: 10px;
  margin: 20px;
}

.pagination-bar {
  justify-content: center;
